<template>
	<div class="accountManRoot">
	  <div class="accountHeader">
	  	<span></span>
	  	<mt-button icon="back" @click.native="$router.go(-1)" class="goBack"></mt-button>
	  </div>
      <div class="" style="margin-top:1rem;">
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell">
              <mt-cell title="" to="//github.com" is-link value="1M"></mt-cell>
            </div>
          </div>
        </div>
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell">
              <mt-cell title="" to="//github.com" is-link value="1111"></mt-cell>
            </div>
          </div>
        </div>
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell" @click="chooseGender($event)">
              <mt-cell title="" is-link :value="gender" ></mt-cell>
            </div>
            <mt-actionsheet :actions="actions" v-model="sheetVisible"> </mt-actionsheet>
          </div>
        </div>
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell" @click="openPicker()">
              <mt-cell title="" is-link :value="birthday"></mt-cell>
            </div>
            <mt-datetime-picker v-model="pickerVisible" type="date" year-format="{value} " month-format="{value} " date-format="{value} "  ref="picker" :startDate="date" @confirm="handleConfirm">
</mt-datetime-picker>
          </div>
        </div>
      </div>

      <div class="" style="margin-top:0.5rem;">
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell">
              <mt-cell title="" to="//github.com" is-link value=""></mt-cell>
            </div>
          </div>
        </div>
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell">
              <mt-cell title="" to="//github.com" is-link value="15622769865"></mt-cell>
            </div>
          </div>
        </div>
      </div>

      <div class="" style="margin-top:0.5rem;">
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell">
              <mt-cell title="" to="//github.com" is-link value=""></mt-cell>
            </div>
          </div>
        </div>
        <div class="accountList">
          <div class="accountContainer">
            <div class="cell">
              <mt-cell title="" to="//github.com" is-link value=""></mt-cell>
            </div>
          </div>
        </div>
      </div>

  </div>
</template>

<script type="es6">
import { Cell } from 'mint-ui'
import { mapMutations } from 'vuex'

export default{
	data(){
		return {
			actions:[
				{name:'',method:this.male},
				{name:'',method:this.female}
			],
			gender:'',
			sheetVisible:false,
			pickerVisible:'',
			date:new Date('1970'),
			birthday:'19700101'
		}
	},
	created(){
		this.setHeadTitle('')
	},
	methods:{
		chooseGender(event){
			this.sheetVisible=true;
		},
    male(){
      this.gender=''
    },
    female(){
      this.gender=''
    },
		openPicker() {
	        this.$refs.picker.open();
	    },
	    handleConfirm(value){
	    	var monthArr=['01','02','03','04','05','06','07','08','09','10','11','12'];
	    	var year=this.pickerVisible.getFullYear();
	    	var month=monthArr[this.pickerVisible.getMonth()];
	    	var date=this.pickerVisible.getDate()
	    	if(this.pickerVisible.getDate()<=9){
	    		date='0'+this.pickerVisible.getDate();
	    	}
	    	this.birthday=year+''+month+date+'';
	    },
		...mapMutations(['setHeadTitle'])
	}
}
</script>
<style lang="scss" scoped>
  .accountContainer{
    width:17rem;
    margin:auto;
  }
  .mint-button--default{
	background-color: transparent !important;
	box-shadow: none !important;
}
  .accountManRoot{
    background-color: #eef2f3;
    min-height: 32rem;
    padding-top: 3.1rem;
    .accountHeader{
    	height: 3.5rem;
    	line-height: 3.5rem;
    	text-align: center;
    	background-color: #fff;
    	position: absolute;
    	left: 0;
    	top: 0;
    	width: 100%;
    	.goBack{
    		position: absolute;
    		left: 0;
    		top:1rem;
    	}
    }
    .accountList{
      background-color: #fff;
      .accountContainer{
         overflow: hidden;
         .v-modal{
	      	z-index: 3000!important;
	      }
        .setLogo{
          line-height: 2rem;
          img{
            height: 1.5rem;
            width: 1.5rem;
            border-radius: 100%;
            background-color: #008cc4;
            vertical-align: middle;
          }
        }
        .cell{
          
        }

      }
    }
  }
  .mint-actionsheet{
        	.mint-actionsheet-button{
        		color:#0057d1!important;
        	}
        }
</style>



