import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    headTitle: '',
    goodsData: null,
    priceData: null,
    itemArray: [],
    userInfo: [],
    addressArr: [],
    timeArr: [],
    goodsList: [],
// 
    searchCriteria: {curCity: '', curCate: 0, timeRange: '', rangeWay: 'hot'}
    // ()()()()
  },
  getters: {
    musicName: state => state.musicName,
    isLogin: state => state.isLogin
  },
  mutations,
  actions
})
export default store



